import React, { useState } from "react";
import Header from "../../components/header/Header";

import "./ResignGuard.css";

import { saveAs } from "file-saver";
import * as docx from "docx";
import { Document, Packer, Paragraph, TextRun, Table, TableRow, TableCell } from "docx";

const ResignGuard = () => {
  // Initial state for editable fields
  const [formData, setFormData] = useState({
    matkhau: "HN - TRE",
    nguoiCatGac: "HUNH HU THNH",
    mucCanhPhong: "CNG PH",
    ngay: "18",
    thang: "01",
    nam: "2024",
    capBac: "i y",
    chucVu: "i i trng",
    donVi: "i i 11",
    sdt: "0987157157",
    rows: [
      { time: "21.30  23.00", chiHuy: "Nguyn Vn A", dungCu: "Di cui", nguoiGac: "Nguyn Vn B", sungAK: "AK 1706p", nguoiDocGac: "Nguyn Vn C" },
      { time: "23.00  00.30", chiHuy: "", dungCu: "", nguoiGac: "", sungAK: "", nguoiDocGac: "" },
      { time: "00.30  02.00", chiHuy: "", dungCu: "", nguoiGac: "", sungAK: "", nguoiDocGac: "" },
      { time: "02.00  03.30", chiHuy: "", dungCu: "", nguoiGac: "", sungAK: "", nguoiDocGac: "" },
      { time: "03.30  05.00", chiHuy: "", dungCu: "", nguoiGac: "", sungAK: "", nguoiDocGac: "" },
    ],
  });

  const handleInputChange = (e, rowIndex, field) => {
    if (rowIndex !== undefined) {
      const updatedRows = formData.rows.map((row, index) =>
        index === rowIndex ? { ...row, [field]: e.target.value } : row
      );
      setFormData({ ...formData, rows: updatedRows });
    } else {
      setFormData({ ...formData, [e.target.name]: e.target.value });
    }
  };

  const exportToDocx = () => {
    const doc = new Document({
      sections: [
        {
          properties: {},
          children: [
            new Paragraph({
              children: [new TextRun({ text: "NG K MC TIU GC", bold: true, size: 32 })],
              alignment: docx.AlignmentType.CENTER,
            }),
            new Paragraph(" "),
            new Table({
              rows: [
                new TableRow({
                  children: [
                    new TableCell({ children: [new Paragraph("Mt khu:")] }),
                    new TableCell({ children: [new Paragraph(formData.matkhau)] }),
                    new TableCell({ children: [new Paragraph("Ngi ct gc:")] }),
                    new TableCell({ children: [new Paragraph(formData.nguoiCatGac)] }),
                  ],
                }),
                new TableRow({
                  children: [
                    new TableCell({ children: [new Paragraph("Ngy:")] }),
                    new TableCell({ children: [new Paragraph(`${formData.ngay}/${formData.thang}/${formData.nam}`)] }),
                    new TableCell({ children: [new Paragraph("Mc canh phng:")] }),
                    new TableCell({ children: [new Paragraph(formData.mucCanhPhong)] }),
                  ],
                }),
                // More rows for the table...
              ],
            }),
            new Paragraph(" "),
            new Table({
              rows: formData.rows.map((row) =>
                new TableRow({
                  children: [
                    new TableCell({ children: [new Paragraph(row.time)] }),
                    new TableCell({ children: [new Paragraph(row.chiHuy)] }),
                    new TableCell({ children: [new Paragraph(row.dungCu)] }),
                    new TableCell({ children: [new Paragraph(row.nguoiGac)] }),
                    new TableCell({ children: [new Paragraph(row.sungAK)] }),
                    new TableCell({ children: [new Paragraph(row.nguoiDocGac)] }),
                  ],
                })
              ),
            }),
          ],
        },
      ],
    });

    Packer.toBlob(doc).then((blob) => {
      saveAs(blob, "DangKyMucGac.docx");
    });
  };

  return (
    <>
      <Header />
      <div className="container-fluid p-10">
        <div id="body-header" className="grid grid-cols-3">
          <div className="text-center">
            <label className="font-bold">Mt khu: </label>
            <input
              className="border p-1 rounded text-red-600 font-bold"
              value={formData.matkhau}
              name="matkhau"
              onChange={handleInputChange}
            />
          </div>
          <div className="text-center">
            <h1 className="text-3xl font-bold">
              NG K MC TIU GC
            </h1>
          </div>
          <div className="text-center">
            <h4 className="text-red-600 font-bold">
              TIU ON 4
            </h4>
          </div>
        </div>
        <div className="flex justify-between items-center">
          <div className="flex flex-collum">
            <label>Ngi ct gc:</label>
            <input
              className="border p-1 rounded"
              value={formData.nguoiCatGac}
              name="nguoiCatGac"
              onChange={handleInputChange}
            />
          </div>
          <div className="flex flex-collum">
            <label>Cp bc:</label>
            <input
              className="border p-1 rounded"
              value={formData.nguoiCatGac}
              name="nguoiCatGac"
              onChange={handleInputChange}
            />
          </div>
          <div className="flex flex-collum">
            <label>Chc v:</label>
            <input
              className="border p-1 rounded"
              value={formData.nguoiCatGac}
              name="nguoiCatGac"
              onChange={handleInputChange}
            />
          </div>
          <div className="flex flex-collum">
            <label>n v:</label>
            <input
              className="border p-1 rounded"
              value={formData.nguoiCatGac}
              name="nguoiCatGac"
              onChange={handleInputChange}
            />
          </div>

        </div>

        <div className="flex justify-between items-center my-4">
          <div className="flex flex-collum">
            <label>Mc canh phng:</label>
            <input
              className="border p-1 rounded"
              value={formData.mucCanhPhong}
              name="mucCanhPhong"
              onChange={handleInputChange}
            />
          </div>
          <div className="flex flex-collum">
            <label>Mc canh phng:</label>
            <input
              className="border p-1 rounded"
              value={formData.mucCanhPhong}
              name="mucCanhPhong"
              onChange={handleInputChange}
            />
          </div>

        </div>
        <div className="flex items-center my-4">
          <div className="flex flex-collum">
            <label>Ngy:</label>
            <input
              className="border p-1 rounded"
              value={formData.ngay}
              name="ngay"
              onChange={handleInputChange}
              style={{ width: '64px' }}
            />
          </div>
          <div className="flex flex-collum">
            <label>Thng:</label>
            <input
              className="border p-1 rounded"
              value={formData.thang}
              name="thang"
              onChange={handleInputChange}
              style={{ width: '64px' }}
            />
          </div>
          <div className="flex flex-collum">
            <label>Nm:</label>
            <input
              className="border p-1 rounded "
              value={formData.nam}
              name="nam"
              onChange={handleInputChange}
              style={{ width: '64px' }}
            />
          </div>
        </div>

        <table className="table-auto w-full border-collapse border border-gray-400">
          <thead>
            <tr>
              <th className="border border-gray-400 p-2">Thi gian</th>
              <th className="border border-gray-400 p-2">Ch huy gc</th>
              <th className="border border-gray-400 p-2">Di cui, Cc h tr</th>
              <th className="border border-gray-400 p-2">Ngi gc</th>
              <th className="border border-gray-400 p-2">Sng AK</th>
              <th className="border border-gray-400 p-2">Ngi c gc, dn gc, chng kin bn giao</th>
            </tr>
          </thead>
          <tbody>
            {formData.rows.map((row, index) => (
              <tr key={index}>
                <td className="border border-gray-400 p-2">{row.time}</td>
                <td className="border border-gray-400 p-2">
                  <input
                    className="border p-1 rounded"
                    value={row.chiHuy}
                    onChange={(e) => handleInputChange(e, index, "chiHuy")}
                  />
                </td>
                <td className="border border-gray-400 p-2">
                  <input
                    className="border p-1 rounded"
                    value={row.dungCu}
                    onChange={(e) => handleInputChange(e, index, "dungCu")}
                  />
                </td>
                <td className="border border-gray-400 p-2">
                  <input
                    className="border p-1 rounded"
                    value={row.nguoiGac}
                    onChange={(e) => handleInputChange(e, index, "nguoiGac")}
                  />
                </td>
                <td className="border border-gray-400 p-2">
                  <input
                    className="border p-1 rounded"
                    value={row.sungAK}
                    onChange={(e) => handleInputChange(e, index, "sungAK")}
                  />
                </td>
                <td className="border border-gray-400 p-2">
                  <input
                    className="border p-1 rounded"
                    value={row.nguoiDocGac}
                    onChange={(e) => handleInputChange(e, index, "nguoiDocGac")}
                  />
                </td>
              </tr>
            ))}
          </tbody>
        </table>

        <button
          onClick={exportToDocx}
          className="mt-4 bg-blue-500 text-white p-2 rounded"
        >
          Xut ra file .doc
        </button>
      </div>
    </>

  );
};

export default ResignGuard;

