const ListTB = () => {
  const rooms = [
    { name: "Phng o to", status: " ng k", approval: "Kim duyt" },
    { name: "Phng Chnh tr", status: "Cha ng k bo ban", approval: "Kim duyt" },
    { name: "Phng HC - KT", status: "Cha ng k bo ban", approval: "Kim duyt" },
  ];

  const departments = [
    { name: "Khoa Chnh tr", status: " ng k", approval: "Kim duyt" },
    { name: "Khoa HC - KT", status: "Cha ng k bo ban", approval: "Kim duyt" },
    { name: "Khoa Binh chng", status: "ang k bo ban", approval: "Kim duyt" },
    { name: "Khoa BCHT", status: "Cha ng k bo ban", approval: "Kim duyt" },
  ];

  const battalions = [
    { name: "Tiu on 1", status: "Cha ng k bo ban", approval: "Kim duyt" },
    { name: "Tiu on 2", status: " ng k", approval: "Kim duyt" },
    { name: "Tiu on 3", status: "Cha ng k bo ban", approval: "Kim duyt" },
    { name: "Tiu on 4", status: "ang k bo ban", approval: "Kim duyt" },
  ];

  const statusOptions = [" ng k", "Cha ng k bo ban", "ang k bo ban"];
  const approvalOptions = ["Kim duyt"];

  const renderRow = (item) => (
    <div className="flex justify-between items-center p-2 border-b">
      <span>{item.name}</span>
      <select className="border p-1 rounded">
        {statusOptions.map((status) => (
          <option key={status} selected={item.status === status}>
            {status}
          </option>
        ))}
      </select>
      <select className="border p-1 rounded">
        {approvalOptions.map((approval) => (
          <option key={approval} selected={item.approval === approval}>
            {approval}
          </option>
        ))}
      </select>
    </div>
  );

  return (
    <>
      <Header />
      <div className="container mx-auto p-4">
        <h2 className="text-xl font-bold mb-4">TRC BAN TC CHIN</h2>

        <h3 className="text-lg font-semibold">Khi Phng</h3>
        {rooms.map(renderRow)}

        <h3 className="text-lg font-semibold mt-4">Khi Khoa</h3>
        {departments.map(renderRow)}

        <h3 className="text-lg font-semibold mt-4">Khi Tiu on</h3>
        {battalions.map(renderRow)}
      </div>
    </>

  );
};

export default ListTB;
